@import "../../styles/variables";
@import "../../styles/layout";
@import "../../styles/index";
@import "../../styles/breakpoints";

.career-page-2 {
  @extend %flex-col-center;
  height: 50vh;
  width: 60vw;
  z-index: 1;

  @include breakpoint(tablet) {
    width: 70vw;
  }

  @include breakpoint(mobile) {
    width: 80vw;
  }

  &__title {
    @extend %flex-col-center;
    margin-bottom: $m-sm;

    span {
      font-size: $font-sm;
      font-weight: bold;
      color: $text-primary-color;
    }
    div {
      font-size: $font-lg;
      font-weight: bold;
      color: $theme-3-primary-color;
    }
  }

  &__description-container {
    @extend %flex-col-center;
    background-color: rgba(255, 255, 255, 0.5);
    padding: $p-sm;
    border-radius: $border-sm;
    &__description {
      font-size: $font-xs;
      font-weight: bold;
      color: $text-primary-color;
    }
  }

  &__sub-types {
    padding: $p-sm;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    &__sub-type {
      background-color: $white-color;
      padding: $p-xs;
      margin: $m-xxxs;
      font-size: $font-xs;
      font-weight: bold;
      border-radius: $border-sm;
      box-shadow: $box-shadow-block;
    }
  }

  &__chart-container {
    @extend %flex-col-center;
    width: 100%;
    &__description {
      font-size: $font-xs;
      color: $theme-3-primary-color;
      font-weight: bold;
      padding-bottom: $p-xxs;
    }
    &__bar {
      @extend %flex-row-center;
      height: 0.75rem;
      width: 100%;
      &--left {
        height: 100%;
        background-color: $career-chart-first-color;
      }
      &--right {
        height: 100%;
        background-color: $career-chart-second-color;
      }
    }
  }

  // &__background-image--left {
  //   position: absolute;
  //   top: 0.5%;
  //   left: 3%;
  //   z-index: -1;
  //   @include breakpoint(mobile) {
  //     left: -20%;
